	import interfaceurl from '@/utils/interface.js'
	export default {
		data() {
			return {
				currentPage: 1,
				pageSize: 20,
				total: 0,
				totalFee: '0.00',
				shopList: [],
				// shopList: [{
				// 	selected: true,
				// 	goods_name: '商品名',
				// 	goods_cover_img: 'https://early-education.oss-cn-beijing.aliyuncs.com/meter_power/default/20200327/0f0bfc1fbb6a8faabd0de3c2b5fbab66.jpg',
				// 	goods_price: 123,
				// 	buy_number: 1
				// }],
				timer: null,
				isAll: true,
				isEdit: false,
				//动态参数
				cartList: {} //购物车列表
			}
		},
		onShow() {
      // this.getShoppingList(1)
			this.getCartPageList();
		},
		onReachBottom() {
		  if(this.shopList.length >= this.total){
			return
		  }
		  this.currentPage++
		  this.getShoppingList(2)
		},
		methods: {
			getCartPageList() {
				let that = this;
				let params = {
					page: 1 ,
					size: 10
				}
				interfaceurl.checkAuth(interfaceurl.cartPageList, params).then((res) => {
					that.cartList = res.data
					for(var item of that.cartList.data) {
						item.selected = true
					}
					that.shopList = that.cartList.data
				});
			},
		  indexPage() {
			uni.switchTab({
				url: '/pages/index/index'
			});
		  },
			getShoppingList(type) {
				apiShoppingList({
					page: this.currentPage,
					size: this.pageSize
				})
				.then((res) => {
					if(res.code == 0) {
						this.total = res.data.total
						if(type == 1) {
					  this.shopList = res.data.data
						}else {
					  this.shopList = this.shopList.concat(res.data.data)
						}
						this.shopList.forEach((item) => {
							item.selected = false
						})
					}
				})
			},
			  handleEdit() {
				this.isEdit = !this.isEdit
			  },
			  handleDelete() {
				const arr = this.shopList.filter((item) => {
				  return item.selected == true
				})
				if(!arr.length) {
				  uni.showToast({
					title: '请选择删除的商品',
					icon: 'none',
					duration: 2000
				  });
				  return
				}
				arr.forEach((item) => {
				  this.confirmDelete(item.cart_id)
				})
			  },
		  confirmDelete(id) {
			apiShoppingDelete({
			  cart_id: id
			})
			.then((res) => {
			  if(res.code == 0) {
				this.getShoppingList(1)
			  }
			})
		  },
			handleSelect(index) {
				this.shopList[index].selected = !this.shopList[index].selected
				this.$set(this.shopList,index,this.shopList[index])
				this.getTotalFee()
			},
			hadleSelectAll() {
				this.isAll = !this.isAll
				if(this.isAll) {
				  this.shopList.forEach((item) => {
					item.selected = true
				  })
				}else {
				  this.shopList.forEach((item) => {
					item.selected = false
				  })
				}
				this.getTotalFee()
			},
			getTotalFee() {
				let total = 0
				this.shopList.forEach((item) => {
					if(item.selected) {
						total = total + item.quantity * parseFloat(item.price)
					}
				})
				this.totalFee = total.toFixed(2)
			},
			shoppingUpdate(item) {
				let that = this;
				let params = {
					cartId: item.cartId, //购物车ID
					quantity: item.quantity
				}
				interfaceurl.checkAuth(interfaceurl.cartUpdate, params, false).then((res) => {
					that.bannerList = res.data
				});
				// apiShoppingUpdate({
				// 	cart_id: item.cart_id,
				// 	buy_number: item.quantity
				// })
				// .then((res) => {
				// 	if(res.code == 0) {

				// 	}
				// })
			},
			reduceNum(index) {
				if (this.shopList[index].quantity <= 1) {
					this.shopList[index].quantity = 1
				}else {
					this.shopList[index].quantity = this.shopList[index].quantity - 1
				}
				clearInterval(this.timer)
				this.timer = setTimeout(()=>{
				  this.shoppingUpdate(this.shopList[index])
				}, 1000)
				this.getTotalFee()
			},
			addNum(index) {
			  this.shopList[index].quantity = this.shopList[index].quantity + 1
			  clearInterval(this.timer)
				this.timer = setTimeout(()=>{
				  this.shoppingUpdate(this.shopList[index])
						}, 1000)
				this.getTotalFee()
			},
			confrimOrderPage() {
				const arr = this.shopList.filter((item) => {
					return item.selected == true
				})
				if(!arr.length) {
				  uni.showToast({
					title: '请选择商品',
					icon: 'none',
					duration: 2000
				  });
					return
				}
				let shopList = []
				arr.forEach((item) => {
				  const shopItem = {
					goods_id: item.spuId,
					goods_price: item.price,
					goods_name: item.name,
					goods_cover_img: item.goodsPhotoUrl,
					shopNum: item.quantity,
					cart_id: item.cartId
				  }
				  shopList.push(shopItem)
				})
				uni.setStorageSync('shopList', shopList)
				uni.navigateTo({
				  url: '/pages/shopping/confirm-order'
				});
			}
		}
	}
